import Circle from './Circle';

const configurations = [
  {
    colour: '#FFC107',
    radius: 100000000,
    pointCount: 3,
    range: { min: 1, max: 1 },
  },
  {
    colour: '#008AD2',
    radius: 250,
    pointCount: 50,
    range: { min: 1, max: 10 },
  },
  {
    colour: '#F44336',
    radius: 150,
    pointCount: 20,
    range: { min: 1, max: 10 },
  },
  {
    colour: '#F44336',
    radius: 90,
    pointCount: 20,
    range: { min: 1, max: 5 },
    compositionOperation: 'xor',
  },
];

export default class {
  constructor(canvas, width, height) {
    const x = width / 2;
    const y = height / 2;
    this.circles = [];
    configurations.forEach((configuration) => {
      this.circles.push(new Circle(
        {
          x,
          y,
          canvas,
          compositionOperation: 'source-over', // default
          ...configuration,
        },
      ));
    });
  }

  animate = () => this.circles.forEach((circle) => circle.draw());
}
