import utils from './utils';

export default class Point {
  constructor(x, y, global, canvas) {
    this.canvas = canvas;
    this.anchor = { x, y };
    this.pos = { x, y };
    this.vel = { x: 0, y: 0 };
    this.global = global;
    this.time = new Date();
    this.duration = utils.random(100, 1000);
    this.addSpring(this.anchor);
    this.friction = 0.98;
    this.direction = this.target(x / 2, y / 2);
    this.setSpeed(utils.random(this.global.min, this.global.max));
    this.setAngle(this.direction);
  }

  getSpeed = () => Math.sqrt(this.vel.x * this.vel.x + this.vel.y * this.vel.y);

  setSpeed(speed) {
    const angle = this.getAngle();
    this.vel.x = Math.cos(angle) * speed;
    this.vel.y = Math.sin(angle) * speed;
  }

  getAngle = () => Math.atan2(this.vel.y, this.vel.x);

  setAngle(angle) {
    const speed = this.getSpeed();
    this.vel.x = Math.cos(angle) * speed;
    this.vel.y = Math.sin(angle) * speed;
  }

  target(x, y) {
    return Math.atan2(y - this.pos.y, x - this.pos.x);
  }

  refresh() {
    this.vel.x *= this.friction;
    this.vel.y *= this.friction;
    this.pos.x += this.vel.x;
    this.pos.y += this.vel.y;
  }

  render() {
    const time = new Date() - this.time;
    if (time < this.duration) {
      this.refreshSpring();
    } else {
      this.time = new Date();
      this.duration = utils.random(1000, 5000);
      this.setSpeed(utils.random(this.global.min, this.global.max));
    }
  }

  addSpring(springTarget) {
    this.springTarget = springTarget;
    this.k = 0.02;
    this.springLength = 0.8;
    this.friction = 0.9;
  }

  refreshSpring() {
    this.vel.x *= this.friction;
    this.vel.y *= this.friction;
    this.pos.x += this.vel.x;
    this.pos.y += this.vel.y;

    const dx = this.springTarget.x - this.pos.x;
    const dy = this.springTarget.y - this.pos.y;
    const distance = Math.sqrt(dx * dx + dy * dy);
    const springForce = (distance - this.springLength) * this.k;
    const ax = dx / distance * springForce;
    const ay = dy / distance * springForce;
    this.vel.x += ax;
    this.vel.y += ay;
  }
}
