import Point from "./Point";

let i;

export default class Circle {
  constructor({
    x,
    y,
    colour,
    radius,
    pointCount,
    range,
    canvas,
    compositionOperation,
  }) {
    this.colour = colour;
    this.pos = { x, y };
    this.radius = radius;
    this.resolution = pointCount;
    this.points = [];
    this.canvas = canvas;
    this.compositionOperation = compositionOperation;

    for (i = 0; i < this.resolution; i += 1) {
      const angle = ((Math.PI * 2) / this.resolution) * i;
      this.points.push(
        new Point(
          Math.cos(angle) * this.radius + this.pos.x,
          Math.sin(angle) * this.radius + this.pos.y,
          range,
          this.canvas
        )
      );
    }
  }

  draw() {
    this.canvas.globalCompositeOperation = this.compositionOperation;
    this.canvas.fillStyle = this.colour;
    this.canvas.strokeStyle = this.colour;
    this.canvas.beginPath();
    let xc = (this.points[0].pos.x + this.points[1].pos.x) / 2;
    let yc = (this.points[0].pos.y + this.points[1].pos.y) / 2;

    this.canvas.moveTo(xc, yc);

    //console.log('=========================')

    for (i = 1; i < this.points.length - 1; i += 1) {
      xc = (this.points[i].pos.x + this.points[i + 1].pos.x) / 2;
      yc = (this.points[i].pos.y + this.points[i + 1].pos.y) / 2;
      //console.log(`quadraticCurveTo: ${i}x > ${i}y`)
      this.canvas.quadraticCurveTo(
        this.points[i].pos.x,
        this.points[i].pos.y,
        xc,
        yc
      );
    }
    xc = (this.points[this.points.length - 1].pos.x + this.points[0].pos.x) / 2;
    yc = (this.points[this.points.length - 1].pos.y + this.points[0].pos.y) / 2;

    //console.log(`quadraticCurveTo: ${i}`)
    this.canvas.quadraticCurveTo(
      this.points[i].pos.x,
      this.points[i].pos.y,
      xc,
      yc
    );

    xc = (this.points[0].pos.x + this.points[1].pos.x) / 2;
    yc = (this.points[0].pos.y + this.points[1].pos.y) / 2;

    //console.log(`quadraticCurveTo: ${i}`)
    this.canvas.quadraticCurveTo(
      this.points[0].pos.x,
      this.points[0].pos.y,
      xc,
      yc
    );
    this.canvas.stroke();
    this.canvas.fill();
    this.points.forEach((points) => points.render());
  }
}
