// Collection of utility functions.
// Exclusively for geometry usage for now.

export default {

  random: (min, max) => min + Math.random() * (max - min),

  randomA: (min, max) => Math.floor(min + Math.random() * (max - min + 1)),

  easing: {
    linear: (n) => n,
    elastic: (n) => n * (33 * n * n * n * n - 106 * n * n * n + 126 * n * n - 67 * n + 15),
    inExpo: (n) => (2 ** (10 * (n - 1))),
  },
};
