import React, { useEffect, useState, useRef } from 'react';
import Cell from '../geometry/Cell';
import utils from '../geometry/utils';
import useCanvas from '../hooks/useCanvas';
import '../App.scss';

function Canvas({ children }) {
  const [canvasRef] = useCanvas();
  const animationRef = useRef({ width: 0, height: 0 });
  const cellRef = useRef(null);
  // https://medium.com/geographit/accessing-react-state-in-event-listeners-with-usestate-and-useref-hooks-8cceee73c559
  const [size, setSize] = useState({ width: 0, height: 0 });
  const sizeRef = useRef(size);

  const updateSize = () => {
    const { parentNode } = canvasRef.current;
    const newSize = {
      ...sizeRef.current,
      width: parentNode.clientWidth,
      height: parentNode.clientHeight,
    };
    sizeRef.current = newSize;
    setSize(newSize);
  };

  function animate() {
    const context = canvasRef.current.getContext('2d');
    const cell = new Cell(context, sizeRef.current.width, sizeRef.current.height);

    const animateCell = () => {
      cell.animate();
      requestAnimationFrame(animateCell);
    };

    requestAnimationFrame(animateCell);
  }

  useEffect(() => {
    updateSize();
    window.addEventListener('resize', updateSize);
    animationRef.current = requestAnimationFrame(() => animate());
    return () => {
      window.removeEventListener('resize', updateSize);
      cancelAnimationFrame(animationRef.current);
    };
  }, []);

  return (
    <div className="canvasContainer" id="container">
      <canvas
        className="canvas hide-if-small"
        ref={canvasRef}
        width={size.width}
        height={size.height}
      />
      {children}
    </div>
  );
}

export default Canvas;
